import { Attachments } from "../../../_models/report";
import AppPreviewModelService from "../../../_service/app-preview-model-service";
import "../../components/app-image-card/image-card.scss";
import AddIcon from '@mui/icons-material/Add';

interface PropsForImageCard {
    images: Attachments[];
}
const ImageCard = (props: PropsForImageCard) => {
    const images = props.images.slice(0, 5);

    const handleClick = () => {
        AppPreviewModelService.createPreviewPopUp({
            headerName: "",
            attachments: images,
            previewSong: null
        });
    };

    return (
        <>
            <div className="image-gallery">
                {images.map((imageSrc, index) => {
                    return <div style={{ position: 'relative' }} key={index}>
                        {/* <AppImage
                            src={imageSrc.url}
                            alt={`Image ${index}`}
                            onClick={() => handleClick()}
                            style={{
                                ...(images.length === 5 && index == images.length - 1 && { opacity: 0.5 }) // Add opacity only to the last image
                            }}
                            placeholder={imagePlaceholder}
                            height={80}
                        /> */}
                        <img src={imageSrc.url} alt={`Image ${index}`} onClick={() => handleClick()}
                            style={{
                                ...(images.length === 5 && index == images.length - 1 && { opacity: 0.5 }) // Add opacity only to the last image
                            }}
                        />
                        {(images.length === 5 && index == images.length - 1 &&
                            <div className="plus-icon">
                                <AddIcon></AddIcon>
                            </div>)}
                    </div>
                })}
            </div>
        </>
    );
};

export default ImageCard;
