
export function getAddress(address1: string, address2?: string): string {
    var address = '';
    if (address1) address += address1;
    if (address2) address += address2;
    return address
}

export function getDate(inputDateString: string): string {
    const date = new Date(inputDateString);

    const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: 'short', day: '2-digit', timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone };
    return date.toLocaleDateString('en-US', options);
}

export function truncateMiddle(text: string, maxLength: number = 30): string {
    if (text.length <= maxLength) {
        return text;
    }

    const truncatedLength = Math.floor((maxLength - 3) / 2); // Adjust for the ellipsis
    const start = text.substring(0, truncatedLength);
    const end = text.substring(text.length - truncatedLength);

    return `${start}...${end}`;
}
