import Button from "../../_shared/components/button/button";
import SegmentControl from "../../_shared/components/segment/segment";
import ReportList from '../../_shared/components/report-list/report-list';
import './flagged-report.scss';
import ReportView from '../../_shared/components/report-view/report-view';
import { ReportAttachments, SegmentModel, ReportDetail, FlaggedReportSlice } from "../../_models/report";
import { ReportedList } from "../../_models/app-common-model";
import UserService from "../../_service/user.service";
import { ReportStatus, requestType } from "../../enum/ReportEnum"
import { Logger } from "../../_core/logger.service";
import React, { Component } from "react";
import AppLoaderService from "../../_service/app-loader-service";
import { reportDetails } from "../../redux/feature/flagged-report-slice";
import { connect } from "react-redux";
import { Query, User } from "../../_models/user";
import AppToasterService from "../../_service/app-alert-service";
import { Message } from "../../_core/constant";
import AppModelService from "../../_service/app-model-service";
import UserProfileCard from "../../_shared/components/user-profile-card/user-profile-card";


const logger = new Logger();

interface reportState {
    segmentData: SegmentModel[];
    userList: ReportedList[];
    isLoading: boolean;
    currentTab: SegmentModel;
    isDisabledSubmit: boolean;
    selectedReport: number;
    selectedQuery: string;
    isRendered: boolean;
    showReport: boolean;
    selectedReportAction: number | null;
    textareaRef: any;
}

// type PropsFromRedux = ConnectedProps<typeof connector>;

interface PropsFromRedux {
    reportDetail: ReportDetail
    reportDetails: (res: any) => void;
}

export class FlaggedReport extends Component<PropsFromRedux, reportState> {
    commentsText: string = '';
    constructor(props: PropsFromRedux) {
        super(props);
        this.state = {
            segmentData: [
                { name: 'Active requests', status: ReportStatus.Active, isSelected: true },
                { name: 'Appealed', status: ReportStatus.Appealed, isSelected: false },
                { name: 'Completed', status: ReportStatus.Completed, isSelected: false }
            ],
            userList: [],
            isLoading: false,
            currentTab: { name: 'Active requests', status: ReportStatus.Active, isSelected: true },
            isDisabledSubmit: true,
            selectedReport: 0,
            selectedQuery: '',
            isRendered: false,
            showReport: true,
            selectedReportAction: null,
            textareaRef: React.createRef()
        }
        this.submitReport = this.submitReport.bind(this);
        this.handleCellClick = this.handleCellClick.bind(this);
        this.reportWarningChange = this.reportWarningChange.bind(this);
        this.callLoader = this.callLoader.bind(this);
    }

    callLoader() {
        this.setState({ isLoading: true });
        setTimeout(() => {
            this.setState({ isLoading: false })
        }, 500);
    }

    componentDidMount() {
        this.fetchRequestList();
    };

    fetchRequestList(searchTerm?: string, status = this.state.currentTab.status) {
        AppLoaderService.showLoader(true);
        const type = requestType.report;
        UserService.getReportList(status, type, searchTerm)
            .then((res: any) => {
                this.setState(prevState => ({
                    ...prevState,
                    userList: res.result,
                    showReport: res && res.result?.length
                }), () => {
                    if (res && res.result?.length) this.fetchReport(res.result[0]?.id);
                    AppLoaderService.showLoader(false);
                });
            })
            .catch((err: any) => {
                logger.debug(err);
                AppLoaderService.showLoader(false);
            })
    }

    handleCellClick = async (reportId: number) => {
        AppLoaderService.showLoader(true);
        this.fetchReport(reportId);
        this.setState({ isRendered: false });
    };

    fetchReport(reportId: number) {
        UserService.fetchDetailsByReportId(reportId)
            .then((res) => {
                this.props.reportDetails(res);
                this.setState(({
                    selectedReport: reportId,
                    isRendered: true,
                    isDisabledSubmit: true
                }));
            })
            .catch((err: any) => {
                logger.debug(err);
            })
            .finally(() => {
                AppLoaderService.showLoader(false);
            })
    }

    segmentSelected(i: number) {
        this.setState(prevState => ({
            segmentData: prevState.segmentData.map((obj: any, index: number) => ({
                ...obj, isSelected: i === index
            })),
            currentTab: prevState.segmentData[i],
            isRendered: false
        }), () => {
            this.fetchRequestList();
        })
    }

    onSearch(value: string) {
        this.fetchRequestList(value)
    }

    handleOptionChange(e: Query) {
        this.setState((prevState: reportState) => ({
            isDisabledSubmit: false,
            selectedQuery: e.question,
            selectedReportAction: e.id
        }), () => {
        })
        this.commentsText = '';
    }

    reportWarningChange(ev: string) {
        this.commentsText = ev;
    }

    saveReport(reportId: number, attachments: ReportAttachments) {
        AppLoaderService.showLoader(true);
        UserService.saveReport(reportId, attachments)
            .then(() => {
                AppToasterService.showToaster({
                    message: Message.successFully,
                    severity: 'success'
                });
                this.setState({ isRendered: false });
                this.fetchRequestList();
            })
            .catch((err) => {
                logger.error(err)
            })
            .finally(() => {
                AppLoaderService.showLoader(false);
            })
    }

    submitReport(e: React.MouseEvent<HTMLButtonElement>) {
        e.preventDefault();
        if (this.state.selectedReportAction == 26 && !this.commentsText) {
            AppToasterService.showToaster({
                message: 'Warning message required',
                severity: 'warning'
            });
            if (this.state.textareaRef) this.state.textareaRef.current.focus();
            return
        }
        this.saveReport(this.state.selectedReport, { action: this.state.selectedQuery, comments: this.commentsText });
    }

    getUsersWithId(params: number[]) {
        AppLoaderService.showLoader(true);
        UserService.getUserList(params).then((res: any) => {
            this.showTagPeoplePopUp(res);
            AppLoaderService.showLoader(false);
        }).catch((error) => {
            AppLoaderService.showLoader(false);
            logger.info(error);
        });
    }

    showTagPeoplePopUp(userList: User[]) {
        AppModelService.createPopUp({
            headerName: 'Tagged in the post',
            header: true,
            footer: false,
            width: 700,
            height: 560,
            compoent: <UserProfileCard userList={userList} />,
            class: 'dialouge-content-container',
            showCloseIcon: false
        });
    }

    render() {
        return (
            <>
                <div className='report-layout'>
                    <div className="justify-spacebetween">
                        <div className='width-50'>
                            {<SegmentControl segments={this.state.segmentData} onSegmentClick={(i) => this.segmentSelected(i)} />}
                        </div>
                        {
                            (this.state.currentTab.status != ReportStatus.Completed) &&
                            <div className='width-50 submit_button_container'>
                                <div className="submit_button">
                                    <Button click={this.submitReport} isDisabled={this.state.isDisabledSubmit}>Submit</Button>
                                </div>
                            </div>
                        }
                    </div>
                    <div className='content'>
                        {
                            <ReportList
                                handleSearch={(e) => this.onSearch(e)}
                                userList={this.state.userList}
                                onCellClick={(e) => this.handleCellClick(e)}
                                isLoading={this.state.isLoading}
                            />
                        }
                        {
                            this.state.showReport ?
                                <ReportView
                                    isRendered={this.state.isRendered}
                                    onChangeOption={(e) => this.handleOptionChange(e)}
                                    warningElementChange={this.reportWarningChange}
                                    taggedPeopleTap={(params) => this.getUsersWithId(params)}
                                    currentTab={this.state.currentTab}
                                    textAreaRef={this.state.textareaRef}
                                /> : ''
                        }
                    </div>
                </div>

            </>
        )
    }
}


const mapState = (state: FlaggedReportSlice) => ({
    reportDetail: state.data
});

const mapDispatch = {
    reportDetails,
};

export default connect(mapState, mapDispatch)(FlaggedReport);
