import { Avatar, Chip, Grid, Typography } from "@mui/material";
import './profile-details.scss';
import { useEffect, useState } from "react";
import UserService from "../../../_service/user.service";
import { Logger } from "../../../_core/logger.service";
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import { Box, Skeleton } from '@mui/material';
import { User } from "../../../_models/user";

const logger = new Logger();

interface PropsForProfile {
    id: number
}

interface UserDetails {
    id: number;
    displayName: string | null;
    name: string;
    interests: string[] | [];
    profilePic: string;
    displayLocationName: string | null;
    status: {
        text: string | null
    }
}

export const ProfileDetails = (props: PropsForProfile) => {
    const [userInfo, setUserInfo] = useState<User>({
        id: 0,
        email: '',
        name: '',
        displayName: '',
        profilePic: '',
        briefcaseCount: 0,
        interests: [],
        interestsCount: 0,
        postsCount: 0,
        status: ''
    });
    const [isLoading, setIsLoading] = useState<boolean>(true);
    const popularity = [
        { id: 0, value: 'Posts' },
        { text: 1, value: 'Briefcase' },
        { text: 2, value: 'Interests' }
    ];

    const details = [
        { text: 0, value: 'Account reported' },
        { text: 1, value: 'Account warnings' },
        { text: 2, value: 'Reports raised' }
    ];

    const fetchData = () => {
        if (!props.id) return;
        UserService.getUserById(props.id)
            .then((res) => {
                setUserInfo(res);
                setIsLoading(false);
            }).catch((error) => {
                logger.error(error)
            });
    }

    useEffect(() => {
        fetchData();
    }, [])

    return (
        <>
            {
                isLoading ?
                    <div>
                        <Box>
                            <Skeleton animation='wave' style={{ height: 40 }} />
                            <div style={{ display: 'flex', marginTop: 30 }}>
                                <Skeleton variant="circular" width={120} height={120} />
                                <div style={{ width: '70%', marginLeft: 20, alignContent: 'center' }}>
                                    <Skeleton style={{ flexGrow: 1, height: 40 }} />
                                    <Skeleton style={{ flexGrow: 1, height: 20 }} />
                                </div>

                            </div>
                        </Box>
                        <Box>
                            <Skeleton variant="rectangular" width={'100%'} height={300} sx={{ marginTop: '5%' }} />
                        </Box>
                    </div>

                    : <div>
                        <div className="header">
                            <Typography className="semiBold-22 header-style">User Profile Details</Typography>
                        </div>
                        <div className="user_profile">
                            <div style={{ display: 'flex' }}>
                                <Avatar sx={{ width: '120px', height: '120px' }} alt={userInfo?.name} src={userInfo?.profilePic} />
                                <Box display={'grid'} gridTemplateColumns={'repeat(3, 1fr)'} sx={{ width: 1 }} >
                                    {
                                        popularity.map((info: any, index: number) => {
                                            return <Box key={index} display={'flex'} alignItems={'center'} justifyContent={'center'} gridColumn={'span 1'}>
                                                <Box sx={{
                                                    width: 1,
                                                    borderRight: index + 1 === popularity.length ? 'unset' : '0.5px solid rgba(142, 142, 142, 0.15)'
                                                }} display={'flex'} flexDirection={'column'} alignItems={'center'}>
                                                    <Typography className="popularity_count">{
                                                        index == 0 ?
                                                            userInfo?.postsCount : index == 1
                                                                ? userInfo?.briefcaseCount : index == 2
                                                                    ? userInfo?.interestsCount
                                                                    : ''
                                                    }</Typography>
                                                    <Typography className="popularity_text">{info.value}</Typography>

                                                </Box>
                                            </Box>
                                        })
                                    }
                                </Box>
                            </div>

                            <Grid container direction={'column'} rowSpacing={1.5} rowGap={0.5}>
                                <Grid item>
                                    <Typography className="regular-22">{userInfo?.displayName ?? userInfo?.name}</Typography>
                                </Grid>
                                {userInfo?.displayLocationName ? <Grid alignItems={'center'} direction={'row'} style={{ display: 'flex' }}>
                                    <LocationOnOutlinedIcon className="pin" />
                                    <Typography className="pin_name">{userInfo?.displayLocationName}</Typography>
                                </Grid> : ''
                                }

                            </Grid>
                            <Box sx={{ width: '100%' }}>
                                <Grid container direction={'column'} rowGap={1.5}>
                                    <Grid item xs>
                                        <Typography className="pin_name">{userInfo?.status}</Typography>
                                    </Grid>
                                    <Grid item xs>
                                        {
                                            userInfo?.interests.map((interest: string, index: number) => {
                                                return <Chip key={index} sx={{
                                                    marginRight: 1.5,
                                                    width: 'fit-content',
                                                    background: 'var(--senary-color)',
                                                    borderRadius: '5px',
                                                    boxShadow: '2px 2px 4px 0px #00000033'
                                                }} className="chip_text" label={interest} />
                                            })
                                        }
                                    </Grid>
                                </Grid>
                            </Box>
                        </div>
                        <Grid container direction={'column'} sx={{ padding: '0 14px' }} rowGap={'10px'}>
                            <Grid item>
                                <Typography className="regular-22">Other Details</Typography>
                            </Grid>
                            <Grid item width={'100px'} height={'1px'} bgcolor={'var(--tertiary-color)'}></Grid>
                            <Grid container sx={{ width: 1 }} display={'grid'} direction={'row'} gridTemplateColumns={'repeat(3, 1fr)'}>
                                {details.map((obj: any, index: number) => {
                                    return <Box gridColumn={'span 1'}>
                                        <Typography className="details_text">{obj.value}:</Typography>
                                        <Typography className="details_value">{
                                            index == 0
                                                ? `${userInfo?.otherDetails?.accountReported} reports` : index == 1
                                                    ? `${userInfo?.otherDetails?.accountWarnings} warnings` : index == 2
                                                        ? `${userInfo?.otherDetails?.accountRaised} reports` : 0
                                        }</Typography>
                                    </Box>
                                })}
                            </Grid>
                        </Grid>
                    </div>
            }
        </>
    )
}

export default ProfileDetails